<template>
<div id="box"><a href="#" id="adds" @click="add">添加</a>
  <table class="my_table">
  <tr id="th">
    <th>会议主题</th>
    <th>会议地址</th>
    <th>会议类别</th>
    <th>开始时间</th>
    <th>操作</th>
  </tr>
  <tr v-for="(item, index) in data" :key="index">
    <td>{{item.theme}}</td>
    <td>{{item.address}}</td>
    <td>{{item.type}}</td>
    <td>{{(item.start_date).slice(0, 10)}}</td>
    <td><a href="#" @click="edit(item)"><i class="fa fa-pencil-square-o" aria-hidden="true"></i></a><span>&nbsp;||&nbsp;</span>
      <a href="#" @click="deleteThis(item.id)"><i class="fa fa-trash" aria-hidden="true"></i></a></td>
  </tr>
</table>
<div class="edit" id="post">
  <div class="form">
    <h2 id="title">添加</h2>
    <div class="inputBx">
      <input type="text" placeholder="会议主题" v-model="theme">
    </div>
    <div class="inputBx">
      <input type="text" placeholder="会议地址" v-model="address">
    </div>
    <div class="inputBx">
      <input type="text" placeholder="会议类别" v-model="type">
    </div>
    <div class="inputBx">
      <input type="text" placeholder="时间" v-model="start_date">
    </div>
    <div class="inputBx">
      <input type="submit" value="提交" id="post_sub" @click="post">
      <input type="submit" value="提交" id="put_sub" @click="putThis">
      <input type="submit" value="取消" @click="showPost">
    </div>
  </div>
</div>
</div>
</template>

<script>
export default {
  name: "lesson",
  data () {
    return {
      data: '',
      theme: '',
      address: '',
      type: '',
      start_date: '',
      id: '',
    }
  },
  methods: {
    getData (){
      const than = this
      than.axios.get('http://127.0.0.1:8000/meeting_class/').then(
        res => {
          // console.log(res.data);
          than.data = res.data
        }
      )
    },

    showPost (){
      const add = document.getElementById('post')
      add.classList.toggle('active')
    },
    add(){
      document.getElementById('title').innerHTML = "添加"
      const put_sub = document.getElementById('put_sub')
      //查看该元素所有样式
      // console.log(window.getComputedStyle(put_sub));
      put_sub.style.display = 'none'
      document.getElementById('post_sub').style.display = 'inline-block'
      this.theme = ''
      this.address = ''
      this.type = ''
      this.start_date = ''
      this.showPost()
    },
    edit(item){
      document.getElementById('title').innerHTML = "修改"
      document.getElementById('post_sub').style.display = 'none'
      document.getElementById('put_sub').style.display = 'inline-block'
      this.theme = item.theme
      this.id= item.id
      this.address = item.address
      this.type = item.type
      this.start_date = (item.start_date).slice(0, 10)
      this.showPost()
    },
    post(){
      const than = this
      than.start_date = than.start_date + ' 00:00:00'
      console.log(than.start_date);
      than.axios.post('http://127.0.0.1:8000/meeting_class/', {
        'theme': than.theme, 'address': than.address, 'type': than.type, 'start_date': than.start_date
      }).then(res => {
        if (res.status === 201){
          console.log('添加成功');
          than.getData()
          than.showPost()
        }
      })
    },
    putThis(){
      const than = this
      than.start_date = than.start_date + ' 00:00:00'
      console.log(than.start_date);
      than.axios.put('http://127.0.0.1:8000/meeting_class/'+than.id+'/', {
        'id':than.id, 'theme': than.theme, 'address': than.address, 'type': than.type, 'start_date': than.start_date
      }).then(res => {
        console.log(res.status)
        if (res.status === 200){
          console.log('添加成功');
          than.getData()
          than.showPost()
        }
      })
    },
    deleteThis(id){
      const than = this
      than.axios.delete('http://127.0.0.1:8000/meeting_class/'+id+'/').then(
        res => {
          console.log(res.status);
          if (res.status === 204){
            console.log('删除成功')
            than.getData()

          }
        }
      )
    }
  },
  created(){
    this.getData()
  }
}
</script>

<style scoped>
#box
{
  position: relative;
  margin: 30px 50px;
}
.my_table
{
  flex: 1;
  position: relative;
  width: 100%;
  background: rgba(255, 255, 255, .7);
  border-collapse: collapse;
  border-spacing: 0;
  overflow: hidden;
  text-align: center;
  box-shadow: 3px 3px 10px rgba(0, 0, 0, .5), -1px -1px 5px rgba(255, 255, 255, .5);
  border-radius: 10px 10px 0 0;
  /*border: 1px;*/
  /*cellpadding: 20px*/
}
table tr
{
  transition: all .2s ease-in;
  cursor: pointer;
}
#th
{
  background: #e50031;
  color: #fff3cd;
}
table th, table td
{
  /*color: #fff3cd;*/
  padding: 10px 0;
  border-bottom: 1px solid rgba(0, 0, 0, .4);
  border-right: 1px solid rgba(0, 0, 0, .1);
}
table th:last-child, table td:last-child
{
  /*border-bottom: none;*/
  border-right: none;
}

table tr:hover
{
  transform: scale(1.02);
  background: rgba(255, 255, 255, .9);
  box-shadow: 2px 2px 6px rgba(0, 0, 0, .2);
}
table tr:hover~table th
{
  /*border-right: 1px solid rgba(0, 0, 0, .9);*/
  color: #fff3cd;
}
.personal table a
{
  text-decoration: none;
}
.edit.active
{
  /*transform: translateY(-65px);*/
  transform: translateX(0px) scale(1);
}
.edit
{
  position: relative;
  /*left: 50%;*/
  /*top: 50%;*/
  min-height: 50vh;
  background: #fff3cd;
  background: rgba(255, 243, 205, .8);
  backdrop-filter: blur(5px);
  margin: 50px;
  transform: translateX(1000px) scale(0.1);
  /*transform: ;*/
  display: flex;
  /*display: none;*/
  justify-content: center;
  align-items: center;
  border-radius: 10px;
  transition: display 0s, transform 0.5s;
  border: 1px solid rgba(255, 255, 255, .8);
  border-bottom: 1px solid rgba(0, 0, 0, .4);
  border-right: 1px solid rgba(0, 0, 0, .4);
  box-shadow: 0px 25px 65px rgba(0, 0, 0, .3);
}
.edit .form
{
  position: relative;
  width: 80%;
  height: 80%;
  padding: 50px;
  display: flex;
  flex-direction: column;
  /*background: #2c3e50;*/
}
.edit .form .inputBx
{
  margin-bottom: 40px;
}
.edit .form .inputBx input
{
  width: 100%;
  background: transparent;
  box-shadow: none;
  border: none;
  outline: none;
  padding: 10px 0;
  font-size: 16px;
  font-weight: 500;
  border-bottom: 2px solid rgba(0, 0, 0, .4);
  color: #12155c;
}
.edit .form .inputBx input::placeholder
{
  color: rgba(0, 0, 0, .2);
}
.edit .form .inputBx input[type='submit']
{
  width: 150px;
  background: #e2ac51;
  color: #1b1e21;
  cursor: pointer;
  font-weight: 400;
  margin: 0 20px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, .4);
}
#box a
{
  display: inline-block;
  width: 50px;
  height: 25px;
  line-height: 25px;
  font-weight: 400;
  font-size: 10px;
  text-decoration: none;
  /*color: #fff3cd;*/
  background: #fff3cd;
  border-radius: 10px;
  margin-bottom: 10px;
  box-shadow: -1px -1px 1px rgba(255, 255, 255, .3), 1px 1px 2px rgba(0, 0, 0, .7), -2px -2px 4px rgba(255, 255, 255, .1),
3px 3px 6px rgba(0, 0, 0, .3);
}
#box a:hover
{
  box-shadow: -1px -1px 1px rgba(255, 255, 255, .3), 1px 1px 2px rgba(0, 0, 0, .7), -2px -2px 4px rgba(255, 255, 255, .1),
  3px 3px 6px rgba(0, 0, 0, .3),
  inset -1px -1px 1px rgba(255, 255, 255, .3),inset 1px 1px 4px rgba(0, 0, 0, .5);
}
</style>
